},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row tarjeta--BG05 px-md-5 px-4"},[_c('div',{staticClass:"row justify-content-center mb-5",attrs:{"data-aos":"flip-up"}},[_c('div',{staticClass:"col-lg-10 col-12"},[_c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-7 col-12 order-lg-1 order-2"},[_c('p',[_vm._v("Uno de los principales desafíos éticos es la presencia de sesgos en los datos. Los modelos de IA aprenden a partir de los datos que se les suministran; si estos contienen prejuicios, estereotipos o representaciones desproporcionadas de ciertos grupos, el modelo replicará y amplificará dichos sesgos en sus resultados. Esto puede llevar a discriminación en decisiones automatizadas como contrataciones, acceso a servicios financieros o diagnósticos médicos. Es fundamental aplicar técnicas de evaluación y corrección de sesgos antes, durante y después del entrenamiento de los modelos.")]),_c('p',[_vm._v("También se debe considerar el respeto a la privacidad y la protección de los datos personales. El uso indebido o no autorizado de datos sensibles puede tener consecuencias legales y éticas graves. Es importante aplicar principios como la minimización de datos, el consentimiento informado, el anonimato y la seguridad en el almacenamiento y procesamiento de la información. Las leyes de protección de datos, como el GDPR en Europa o la Ley 1581 de 2012 en Colombia, establecen directrices claras para proteger los derechos de los ciudadanos.")])]),_c('div',{staticClass:"col-lg-5 col-md-7 col-12 mb-3 order-lg-2 order-1 mb-lg-0"},[_c('div',{staticClass:"row justify-content-center",attrs:{"data-aos":"flip-up"}},[_c('div',{staticClass:"col"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/img21.png"),"alt":"Imagen decorativa"}})])])])])]),_c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-10 col-12 order-lg-2 order-2"},[_c('div',{staticClass:"cajon cajon C01 color-primario p-4 mb-3"},[_c('p',{staticClass:"mb-0"},[_vm._v("Otro aspecto crucial es la explicabilidad y transparencia de los modelos. Muchas veces, los algoritmos se comportan como “cajas negras”, dificultando la comprensión de cómo llegan a ciertas conclusiones. La explicabilidad permite que los usuarios y auditores entiendan los criterios utilizados en las decisiones automatizadas, lo cual es esencial para generar confianza y para corregir errores o sesgos indeseados.")])])])])])])])
